.about {
  padding: 2rem;
  background-color: #191718;
  color: #f3d849;
  font-family: "Cinzel", serif;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  margin: 0 auto;
}

.images-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

.images-container img {
  width: calc(25% - 20px);
  max-width: 100%;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
.about h1 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 20px;
  color: #0a62d0;
  font-family: "Orbitron", sans-serif;
}
.about section {
  margin-bottom: 20px;
}
.about h2 {
  margin-bottom: 10px;
  border-bottom: 2px solid #0a62d0;
  display: inline-block;
}
.about p {
  font-size: 1.2rem;
  color: #e0e5e9;
}

.timeline {
  margin-left: 20px;
}

.timeline-item {
  margin-bottom: 15px;
}

.timeline-item h3 {
  color: #f3d849;
}
@media (max-width: 768px) {
  .images-container img {
    width: calc(50% - 20px);
  }
}

@media (max-width: 480px) {
  .images-container img {
    width: 100%;
  }
}
